import React from 'react';
import { useSelector } from 'react-redux';

function Description() {

    // fetch data from store :
    const WhetherSelector = useSelector(({whether}) => whether)
    

    return (
        <div>
                    <div className="flex flex-row items-center justify-center mt-6">
                        <div className="font-medium text-6xl">{ Math.round(WhetherSelector.main.feels_like) }C</div>
                        <div className="flex flex-col items-center ml-6">
                            <div>{ WhetherSelector.weather[0].main }</div>
                            <div>{ WhetherSelector.weather[0].description }</div>
                                <div className="mt-1">
                                <span className="text-sm"><i className="far fa-long-arrow-up"></i></span>
                                <span className="text-sm font-light text-gray-500">{Math.round(WhetherSelector.main.temp_max)}C</span>
                            </div>
                            <div>
                                <span className="text-sm"><i className="far fa-long-arrow-down"></i></span>
                                <span className="text-sm font-light text-gray-500">{Math.round(WhetherSelector.main.temp_min)}C</span>
                            </div>
                        </div>
                    </div>
        </div>
    );
}

export default Description;