import React, { useRef, useState } from 'react';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import { useDispatch } from 'react-redux';
import { setWhether } from '../Features/WhetherSlice';

function SearchBar() {
    const searchRef = useRef();

    const GEO_API_KEY = process.env.REACT_APP_GEO_API_KEY
    const WHEATHER_API_KEY = process.env.REACT_APP_WHEATHER_API

    // set unites of temperateur 
    const [unite , setunite] = useState('metric')
    // set unites of temperateur 



    const [cities , setcities] = useState([])

    // set information by the fucntion inside the reducer
    const dispatch = useDispatch()



    const handleChange = () => {
        let searchValue = searchRef.current.value;
        if (searchValue !== '') {
            fetch(`https://api.geoapify.com/v1/geocode/autocomplete?text=${searchValue}&apiKey=${GEO_API_KEY}`)
                .then(response => response.json())
                .then(result => {
                    const newCities = result.features.map(data => {
                        let { lat, lon, city, country , formatted } = data.properties;
                        return { lat, lon, city, country , formatted };
                    });
                    setcities(newCities);
                })
                .catch(error => console.log('error', error));
        } else {
            setcities([]);
        }
        // console.log(cities);
    };

    

    const handleSelect = (event, value) => {
        if(value !== null){ 
            const { lat , lon } = value 
            fetch(`https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lon}&units=${unite}&appid=${WHEATHER_API_KEY}`)
                .then(response => response.json())
                .then(result => {
                    const {clouds , main , visibility , name , sys , weather , wind} = result
                    dispatch(setWhether({ clouds , main , visibility , name , sys , weather , wind }))
                })
                .catch(error => console.log('error', error));     
        } 
    };

    

    return (
        <div className='flex'> 
        
            <label htmlFor="simple-search" className="sr-only">Search</label>
            <div className="relative w-full">


            <Autocomplete
            disablePortal
            clearOnBlur={false}
            id="combo-box-demo"
            options={cities}
            isOptionEqualToValue={(option)=>option}
            getOptionLabel={(option) => option.formatted}
            onChange={handleSelect}
            sx={{ width: 260 , border : "none" }}
            renderInput={(params) => (
                <TextField
                    {...params}
                    label="Cities"
                    inputRef={searchRef}
                    onChange={handleChange}
                />
            )}
        />


            </div>

        </div>
    );
}

export default SearchBar;