import React from 'react';
import {motion} from 'framer-motion';
import { useSelector } from 'react-redux';

function Image() {
    const WhetherSelector = useSelector(({whether}) => whether)

    return (
        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' , background:'#002244' , borderRadius : '2pc' }}>
            
            <motion.img 
                whileHover={{
                    scale: 1.1,
                    rotate: 3,
                }} 
                src={`https://openweathermap.org/img/wn/${WhetherSelector.weather[0].icon}.png`}
            /> 

        </div>
    );
}

export default Image;