import { createSlice } from "@reduxjs/toolkit";


const initialState = {
    clouds : undefined, 
    main : {
        feels_like : undefined ,
        humidity : undefined,
    } ,
    name : undefined , 
    sys : undefined ,
    weather : [
        {
            main : undefined ,
            description : undefined ,
            icon : undefined
        }
    ]
    ,
    visibility : undefined ,
    wind : {
        speed : undefined , 
    }
}


const whetherSlice = createSlice({
    name : 'whether', // give name unique to slice 
    initialState : initialState , 
    reducers : {
            setWhether : (state , action) => {  
                state.clouds = action.payload.clouds
                state.main = action.payload.main
                state.visibility = action.payload.visibility
                state.name = action.payload.name
                state.sys = action.payload.sys
                state.weather = action.payload.weather
                state.wind = action.payload.wind
                // console.log(action)
            }
    }
})

export const { resetWhether  , setWhether } = whetherSlice.actions 

export default whetherSlice.reducer